import { changeTemplate } from './utils/changeTemplate';
import writeClientResults from './firebasePost';

//Templates
//Questions
import initialQuestion from './templates/initialQuestion.hbs';
import firstQuestion from './templates/firstQuestion.hbs';
import secondQuestion from './templates/secondQuestion.hbs';
import thirdQuestion from './templates/thirdQuestion.hbs';
import fourthQuestion from './templates/fourthQuestion.hbs';
import fifthQuestion from './templates/fifthQuestion.hbs';
import sixthQuestion from './templates/sixthQuestion.hbs';
import seventhQuestion from './templates/seventhQuestion.hbs';
import eighthQuestion from './templates/eighthQuestion.hbs';
import goodResults from './templates/goodResults.hbs';
import badResults from './templates/badResults.hbs';

//Overlays
import emptyAge from './templates/overlays/emptyAge.hbs';
import invalidAge from './templates/overlays/invalidAge.hbs';
import succesSend from './templates/overlays/succsesSend.hbs';

const refs = {
  container: document.querySelector('.js-container'),
  startButton: document.querySelector('.js-start-button'),
  overlay: document.querySelector('.js-overlay'),
  overlayContainer: document.querySelector('.js-overlay-container'),
};

const answers = {
  name: null,
  email: null,
  age: null,
  risk: null,
  questions: [],
};

let isHasRisk = false;

refs.startButton.addEventListener('click', handleStartButtonClick);

function handleStartButtonClick() {
  changeTemplate(refs.container, initialQuestion);
  refs.initialQuestionBtn = document.querySelector('.js-next-button');
  refs.inputAge = document.querySelector('.js-age-input');
  refs.initialQuestionBtn.addEventListener('click', handleAgeSubmit);
}

function handleAgeSubmit() {
  const childAge = Number(refs.inputAge.value);
  if (childAge < 0){
    refs.overlayContainer.innerHTML = invalidAge();
    refs.overlay.classList.remove('overlay-hidden');
    setInterval(() => {
      refs.overlay.classList.add('overlay-hidden');
      refs.overlayContainer.innerHTML = '';
    }, 2000);
    refs.inputAge.value = '';
    return
  } else if (childAge === 0){
    refs.overlayContainer.innerHTML = emptyAge();
    refs.overlay.classList.remove('overlay-hidden');
    setInterval(() => {
      refs.overlay.classList.add('overlay-hidden');
      refs.overlayContainer.innerHTML = '';
    }, 2000);
    return
  }
  answers.age = childAge;
  changeTemplate(refs.container, firstQuestion);
  refs.questionTitle = document.querySelector('.js-question-title');
  refs.buttonsContainer = document.querySelector('.js-buttons-container');
  refs.buttonsContainer.addEventListener('click', handleButtonAnswerClick);
}

function handleButtonAnswerClick(e) {
  const { questions } = answers;
  const numberOfQuestion = refs.questionTitle.dataset.question;
  const answerButtonData = e.target.dataset.answer;

  if (!answerButtonData) {
    return;
  }

  const answerObject = {
    question: refs.questionTitle.innerText,
    answer: e.target.dataset.answer,
  };

  questions.push(answerObject);

  switch (numberOfQuestion) {
    case '1':
      if (answerButtonData === 'Yes') {
        isHasRisk = true;
      }
      changeTemplate(refs.container, secondQuestion);
      resetEventListeners();
      break;

    case '2':
      if (answerButtonData === 'Yes') {
        isHasRisk = true;
      }
      changeTemplate(refs.container, thirdQuestion);
      resetEventListeners();
      break;

    case '3':
      if (answerButtonData === 'Yes') {
        isHasRisk = true;
      }
      changeTemplate(refs.container, fourthQuestion);
      resetEventListeners();
      break;

    case '4':
      if (answerButtonData === 'Yes') {
        isHasRisk = true;
      }
      changeTemplate(refs.container, fifthQuestion);
      resetEventListeners();
      break;

    case '5':
      changeTemplate(refs.container, sixthQuestion);
      resetEventListeners();
      break;

    case '6':
      changeTemplate(refs.container, seventhQuestion);
      resetEventListeners();
      break;

    case '7':
      changeTemplate(refs.container, eighthQuestion);
      resetEventListeners();
      break;

    case '8':
      if(isHasRisk){
        refs.container.innerHTML = badResults();
      } else {
        refs.container.innerHTML = goodResults();
      }
      refs.questionForm = document.querySelector('.js-form');
      refs.inputName = document.querySelector('.js-input-name');
      refs.inputEmail = document.querySelector('.js-input-email');
      refs.questionForm.addEventListener('submit', handleFormSubmit);


      break; 
  }
}

function resetEventListeners() {
  refs.questionTitle = document.querySelector('.js-question-title');
  refs.buttonsContainer = document.querySelector('.js-buttons-container');
  refs.buttonsContainer.addEventListener('click', handleButtonAnswerClick);
}

function handleFormSubmit (e) {
  e.preventDefault();

  answers.name = refs.inputName.value;
  answers.email = refs.inputEmail.value;
  answers.risk = isHasRisk? "High" : "Low";

  writeClientResults(answers);

  refs.overlayContainer.innerHTML = succesSend();
  refs.overlay.classList.remove('overlay-hidden');
  setInterval(() => {
    refs.overlay.classList.add('overlay-hidden');
    refs.overlayContainer.innerHTML = '';
  }, 2500);
}
